<template>
  <div class="cart-good-item">
    <!-- for循环遍历出，在该店铺下的所有清单-->
    <div class="item" v-for="(item,index) in cartList" :key="index">
      <!-- 通过v-model就可以实现双向绑定，实时刷新数据-->
      <input type="checkbox" class="good-checkbox" :checked="item.isChecked"
             v-model="item.isChecked">
      <!-- 商品详细数据-->
      <div class="cartgood">
        <!-- 图片-->
        <div class="img">
          <img :src="item.image"
               alt="">
        </div>
        <!-- 描述-->
        <div class="goods-desc">
          <div class="goodsName">{{ item.title }}</div>
          <div class="speci">{{ speci(item) }}</div>
          <!--价格 和 计数器-->
          <div class="price-count">
            <div class="price">
              <span class="icon">￥</span>{{ item.price }}
            </div>
            <!-- 计数器-->
            <div class="counter">
              <span class="sub" @click="subCount(item)" :style="subCountStyle(item)"></span>
              <span class="count">{{ item.count }}</span>
              <span class="add" @click="addCount(item)"></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CartGoodItem",
  props: {},
  computed: {
    cartList() {
      //console.log('将要展示的cartList==>', this.$store.state.cartList)
      // 从vuex中查找数据
      return this.$store.state.cartList;
    },
    count() {
      return this.$store.state.cartList.length;
    },
    speci() {
      return function (item) {
        //颜色：牛仔深蓝色；尺码：L；
        return '颜色：' + item.color.name + '；尺码：' + item.size.name + '；'
      }
    },
    subCountStyle() {
      return function (item) {
        if (item.count === 1) {
          return {color: '#eaeaea'}
        } else {
          return {color: '#ababab'}
        }
      }
    }
  },
  methods: {
    // 查找商品，减少数量
    subCount(item) {
      if (item.count !== 1) {
        item.count--;
      }
    },
    // 查找商品，增加数量
    addCount(item) {
      // 并不推荐直接修改store中的数据
      item.count++;
    },

  }
}
</script>

