<template>
  <div class="cart-bottom-bar">
    <div class="select-all">
      <input id="select-all" type="checkbox" class="good-checkbox"
             :checked="isAllSelected"
             @change="selectedAllItem">
      <label for="select-all">
        全选<span class="count">（{{ selectedCount }}）</span>
      </label>
    </div>
    <div class="pay">
      <div class="total-price">
        <span class="q">￥</span>
        <span class="price">{{ totalPrice.toFixed(2) }}</span>
      </div>
      <div class="checkout">结算</div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: "CartBottomBar",
  data() {
    return {
      //totalPrice:'0.00',
    }
  },
  computed: {
    // 引入vuex中的getters到本vue实例中的computed中使用
    ...mapGetters({
      totalPrice:'totalPrice',
      cartList: 'getCartList',
      isAllSelected: 'isAllSelected',
      // 已选择的item数量
      selectedCount: 'selectedCount',
      // 购物车中所有的item数量
      length: 'getCount'
    }),

  },
  methods: {
    // 全选时监听
    selectedAllItem() {
      // 全部选中时，遍历所有的item，设置isChecked为true
      if (this.isAllSelected === false) {
        console.log('全部选中');
        for (let i = 0; i < this.length; i++) {
          this.cartList[i].isChecked = true;
        }
      } else {
        console.log('取消全选');
        for (let i = 0; i < this.length; i++) {
          this.cartList[i].isChecked = false;
        }
      }
    }
  }
}
</script>

