<template>
  <!-- for循环遍历出，各个店铺下的订单-->
  <div class="cart-shop-item">
    <!-- 商店信息 全选按钮-->
    <div class="shop-title">
      <input type="checkbox" class="shop-checkbox" :checked="isAllSelected"
             @change="selectedAllItem">
      <a href="#" class="name">{{ cartList[0].shopName }}</a>
    </div>
    <!-- 该店铺下的商品数据 数据从vuex中获取，不需要传递-->
    <cart-good-item />
  </div>
</template>

<script>
import CartGoodItem from "@/views/cart/cartChild/CartGoodItem";

import {mapGetters} from 'vuex'

export default {
  name: "CartShopItem",
  components: {
    CartGoodItem,

  },
  computed: {
    // =>参数使用数组方式，名字要与getters中，定义的相同！
    //...mapGetters(['getCartList']),
    // 如果想把getters中的方法，重新定义名字的话，参数使用对象形式
    ...mapGetters({
      cartList: 'getCartList',
      isAllSelected: 'isAllSelected',
      length: 'getCount'
    })
    /*cartList() {
      console.log('将要展示的cartList==>', this.$store.state.cartList)
      // 从vuex中查找数据
      return this.$store.state.cartList;
    },*/
  },
  methods: {
    // 全选时监听
    selectedAllItem() {
      // 全部选中时，遍历所有的item，设置isChecked为true
      if (this.isAllSelected === false) {
        console.log('全部选中');
        for (let i = 0; i < this.length; i++) {
          this.cartList[i].isChecked = true;
        }
      } else {
        console.log('取消全选');
        for (let i = 0; i < this.length; i++) {
          this.cartList[i].isChecked = false;
        }
      }
    }
  },
}
</script>

