<template>
  <div class="cart m-icon" v-if="isShow">
    <cart-nav-bar :count="getCount"/>
    <div class="content">
      <cart-shop-item/>
    </div>
    <cart-bottom-bar/>
  </div>
</template>

<script>
import CartNavBar from "@/views/cart/cartChild/CartNavBar";
import CartShopItem from "@/views/cart/cartChild/CartShopItem";
import CartBottomBar from "@/views/cart/cartChild/CartBottomBar";
import {TEST_PUSH} from "@/store/mutations-types";

import {mapGetters} from 'vuex'

export default {
  name: "Cart",
  components: {
    CartNavBar,
    CartShopItem,
    CartBottomBar
  },
  data() {
    return {}
  },
  computed: {
    isShow() {
      return Object.keys(this.$store.state.cartList).length !== 0;
    },
    // 直接将getters里面的方法，转换成计算属性使用。参数有两种形式
    ...mapGetters(['getCartList','getCount']),

    /*// 不同商品的数量
    count() {
      return this.$store.getters.count;
    },
    cartList() {
      this.$store.getters.getCartList;
    },*/
  },
  props: {},
  methods: {},
  created() {
    this.$store.commit(TEST_PUSH);
  }
}
</script>

<style scoped>
.cart {
  height: 100vh;
  background-color: #efefef;
}

.content {
  padding: 46px 0 108px;
  background-color: #efefef;
